* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: "Titillium Web", sans-serif;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0) !important; 
    -webkit-focus-ring-color: rgba(255, 255, 255, 0) !important; 
    outline: none !important;
  }
  
  :root {
    --mainColor: #ffffff;
    --mainColorLight: #0d4b6d;
  
    --textColor: rgb(0, 0, 0);
  }
  
  header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
    padding: 0 5rem;
    background-color: var(--mainColor);
    color: var(--textColor);
  }
  
  #nav-styles {
    margin-left: 20%;
    z-index: 2;
  }
  
  #navbar-item {
    font-size: large;
    color: #0d4b6d;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  }
  
  #navtop{
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
      border-radius: 12px;
      margin-bottom: 12px;
      
      
  }
  
  #navbar-item:hover {
     color: #00b4d8;
    }

    #navbar-item:active {
      color: #00b4d8;
     }
  
  nav a {
    margin: 0 1rem;
    color: var(--textColor);
    text-decoration: none;
  }
  
  nav a:hover {
    color: var(--secondaryColor);
  }
  
  header .nav-btn {
    padding: 5px;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    color: var(--textColor);
    visibility: hidden;
    opacity: 0;
    font-size: 1.8rem;
  }
  
  header div,
  nav {
    display: flex;
    align-items: center;
  }
  @media only screen and (max-width: 930px) {
    header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 80px;
      padding: 0 1rem;
      background-color: var(--mainColor);
      color: var(--textColor);
    }

    header .nav-btn {
      visibility: visible;
      opacity: 1;
    }
  
    #nav-styles {
      margin-left: 0px;
    }
    #navbar-item {
      font-size: large;
    }
  
    header nav {
      position: fixed;
      top: -100vh;
      left: 0;
      height: 100%;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 1.5rem;
      background-color: var(--mainColor);
      transition: 1s;
    }
  
    header .responsive_nav {
      transform: translateY(100vh);
    }
  
    nav .nav-close-btn {
      position: absolute;
      top: 2rem;
      right: 2rem;
    }
  
    nav a {
      font-size: 1.5rem;
    }
  }